.Restaurants-cardsNew{
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
    margin-bottom: 80px;
}
.Restaurants-title{
    display: flex;
    justify-content: flex-start;
    .title{
        font-size: large;
        font-weight: bold;
    }
}
.directionrCards{
    display: flex;
    flex-direction: column;
    margin-left: -88px;
}
.cards{  
    margin-right: 15px;
    background-color: white;
    border-radius: 20px;
    padding: 5px;
    filter: drop-shadow(0px 3px 15px rgba(0, 0, 0, 0.1));
    margin-top: 50px;
    width: 280px;
    height: 250px;
    margin-bottom: -35px;
    
    a{
        text-decoration: none;
    }
    img{
        width: 100%;
        height: 190px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        object-fit: cover;
    }
    .card-content{
        justify-content: space-between;
        padding-left: 5px;
        .card-txt {
            height: 50px; 
            color: black;
        }
        .card-title{
            height : 10px;
            margin-top: 5px;
            margin-bottom: 4px;
            font-size: large;
            font-weight: bold;
        }
        .card-subtitle{
            font-size: medium;
        }
    }
    .icone-Coeurvide{
        display: flex;
        float : right;
        width : 40px;
        height: 40px;
        margin-top : -40px;
        color : black;
    }
}